<template>
    <div>
        <div class="box box-primary">
            <div class="box-header with-border">
                <i class="fa fa-smile-o"></i>
                <h3 class="box-title">Service</h3>
            </div>
            <!-- /.box-header -->
            <div class="box-body">
                <div class="alert alert-success alert-dismissible">
                    <button type="button" class="close" data-dismiss="alert" aria-hidden="true"></button>
                    <h4>
                        <i class="icon fa fa-check"></i> 系统安全运行中
                    </h4>The system is in safe operation, there is no warning information！
                </div>
                <!-- <div class="alert alert-danger alert-dismissible">
                    <button type="button" class="close" data-dismiss="alert" aria-hidden="true">×</button>
                    <h4>
                        <i class="icon fa fa-ban"></i> 爬虫服务
                    </h4>Danger alert preview. This alert is dismissable. A wonderful serenity has taken possession of my entire
                    soul, like these sweet mornings of spring which I enjoy with my whole heart.
                </div>
                <div class="alert alert-info alert-dismissible">
                    <button type="button" class="close" data-dismiss="alert" aria-hidden="true">×</button>
                    <h4>
                        <i class="icon fa fa-info"></i> 定时服务
                    </h4>Info alert preview. This alert is dismissable.
                </div>
                <div class="alert alert-warning alert-dismissible">
                    <button type="button" class="close" data-dismiss="alert" aria-hidden="true">×</button>
                    <h4>
                        <i class="icon fa fa-warning"></i> Alert!
                    </h4>Warning alert preview. This alert is dismissable.
                </div>-->
            </div>
            <!-- /.box-body -->
        </div>
        <!-- Custom Tabs -->
        <div class="nav-tabs-custom">
            <ul class="nav nav-tabs">
                <li class="active">
                    <a href="#tab_1" data-toggle="tab">登录日志</a>
                </li>
                <li>
                    <a href="#tab_2" data-toggle="tab" @click.prevent="warning">爬取日志</a>
                </li>
                <li>
                    <a href="#tab_3" data-toggle="tab" @click.prevent="warning">服务日志</a>
                </li>

                <li class="pull-right">
                    <a href="#" class="text-muted">
                        <i class="fa fa-gear"></i>
                    </a>
                </li>
            </ul>
            <div class="tab-content">
                <div class="tab-pane active" id="tab_1">
                    <div class="box-body table-responsive no-padding">
                        <table class="table table-hover text-center">
                            <tbody>
                                <tr>
                                    <th>用户ID</th>
                                    <th>用户名</th>
                                    <th>登录日期</th>
                                    <th>登录Ip</th>
                                    <th>账户创建日期</th>
                                    <th>账户状态</th>
                                </tr>
                                <tr v-for="(item, index) in loginLogs" :key="index">
                                    <td>{{ item.id }}</td>
                                    <td>
                                        <span class="label label-success">{{ item.username }}</span>
                                    </td>
                                    <td>{{ item.log_create_time }}</td>
                                    <td>{{ item.ip }}</td>
                                    <td>{{ item.account_create_time }}</td>
                                    <td v-if="item.active" class="text-success">
                                        <strong>
                                            <i class="fa fa-check-circle-o text-success"></i> 激活中
                                        </strong>
                                    </td>
                                    <td v-if="item.active!=true" class="text-danger">
                                        <strong>
                                            <i class="fa fa-exclamation-circle text-danger"></i> 禁用中
                                        </strong>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                        <!-- /.box -->
                    </div>
                </div>
                <!-- /.tab-pane -->
                <div class="tab-pane" id="tab_2"></div>
                <!-- /.tab-pane -->
                <div class="tab-pane" id="tab_3"></div>
                <!-- /.tab-pane -->
            </div>
            <!-- /.tab-content -->
        </div>
        <!-- nav-tabs-custom -->

        <warn-tip v-model="showWarnMask" :para="warnInfo"></warn-tip>
    </div>
</template>

<script>
import WarningTip from "./formComponents/WarningTip";
export default {
    data() {
        return {
            loginLogs: [],
            showWarnMask: false,
            warnInfo: {}
        };
    },
    created() {
        this.getLoginLog();
    },
    components: {
        "warn-tip": WarningTip
    },
    methods: {
        warning() {
            this.showWarnMask = true;
            this.warnInfo["title"] = "权限不足";
            this.warnInfo["info"] =
                "Sorry, you are not authorized to read the system log information.";
        },
        getLoginLog() {
            this.$http.get("log.login_log").then(res => {
                if (res.body.status) {
                    this.loginLogs = res.body.login_info;
                } else {
                    this.$notify({
                        title: res.body.message,
                        type: "warning"
                    });
                }
            });
        }
    }
};
</script>

<style lang="">
</style>



