<template>
    <div class="box">
        <div class="box-header with-border">
            <h3 class="box-title">报告图</h3>

            <div class="box-tools pull-right">
                <button type="button" class="btn btn-box-tool" data-widget="collapse">
                    <i class="fa fa-minus"></i>
                </button>
                <button type="button" class="btn btn-box-tool" data-widget="remove">
                    <i class="fa fa-times"></i>
                </button>
            </div>
        </div>
        <!-- /.box-header -->
        <div class="box-body">
            <div class="row">
                <div class="col-md-8">
                    <div class="chart">
                        <!-- Sales Chart Canvas -->
                        <canvas
                            id="salesChart"
                            style="height: 180px; width: 816px;"
                            height="280"
                            width="1020"
                        ></canvas>
                    </div>
                    <!-- /.chart-responsive -->
                </div>
                <!-- /.col -->
                <spider-scale></spider-scale>

                <!-- /.col -->
            </div>
            <!-- /.row -->
        </div>
    </div>
</template>


<script>
import Chart from "chart.js";
import SpiderScale from "./SpiderScale";
export default {
    data() {
        return {
            flag: false,
            labels: [],
            ip66: [],
            kuaiDaili: [],
            ip3366: [],
            xiCi: [],
        };
    },
    mounted() {
        this.getChartDatas();
    },
    watch: {
        flag() {
            this.drawChart();
        }
    },
    components: {
        "spider-scale": SpiderScale
    },
    methods: {
        getChartDatas() {
            this.$http.get("dashboard.line_chart").then(res => {
                if (res.body.status) {
                    this.labels = res.body.label;
                    this.ip66 = res.body.ip66;
                    this.kuaiDaili = res.body.kuaidaili;
                    this.ip3366 = res.body.ip3366;
                    this.xiCi = res.body.xicidaili;
                    this.flag = true;
                }
            });
        },
        drawChart() {
            this.getChartDatas();
            var salesChartCanvas = $("#salesChart")
                .get(0)
                .getContext("2d");

            var salesChartData = {
                labels: this.labels,
                datasets: [
                    {
                        label: "66Ip",
                        borderColor: "#DD4B39",
                        pointBackgroundColor: "#DD4B39",
                        pointBorderColor: "#DD4B39",
                        // backgroundColor: '#DD4B39',
                        data: this.ip66
                    },
                    {
                        label: "kuaiDaili",
                        borderColor: "#00A65A",
                        pointBackgroundColor: "#00A65A",
                        pointBorderColor: "#00A65A",
                        // backgroundColor: '#00A65A',
                        data: this.kuaiDaili
                    },
                    {
                        label: "Ip3366",
                        borderColor: "#F39C12",
                        pointBackgroundColor: "#F39C12",
                        pointBorderColor: "#F39C12",
                        // backgroundColor: '#F39C12',
                        data: this.ip3366
                    },
                    {
                        label: "西刺代理",
                        borderColor: "#00C0EF",
                        pointBackgroundColor: "#00C0EF",
                        pointBorderColor: "#00C0EF",
                        // backgroundColor: '#00C0EF',
                        data: this.xiCi
                    },
                ]
            };

            var salesChartOptions = {
                // Boolean - If we should show the scale at all
                showScale: true,
                // Boolean - Whether grid lines are shown across the chart
                scaleShowGridLines: false,
                // String - Colour of the grid lines
                scaleGridLineColor: "rgba(0,0,0,.05)",
                // Number - Width of the grid lines
                scaleGridLineWidth: 1,
                // Boolean - Whether to show horizontal lines (except X axis)
                scaleShowHorizontalLines: true,
                // Boolean - Whether to show vertical lines (except Y axis)
                scaleShowVerticalLines: true,
                // Boolean - Whether the line is curved between points
                bezierCurve: true,
                // Number - Tension of the bezier curve between points
                bezierCurveTension: 0.3,
                // Boolean - Whether to show a dot for each point
                pointDot: false,
                // Number - Radius of each point dot in pixels
                pointDotRadius: 4,
                // Number - Pixel width of point dot stroke
                pointDotStrokeWidth: 1,
                // Number - amount extra to add to the radius to cater for hit detection outside the drawn point
                pointHitDetectionRadius: 20,
                // Boolean - Whether to show a stroke for datasets
                datasetStroke: true,
                // Number - Pixel width of dataset stroke
                datasetStrokeWidth: 2,
                // Boolean - Whether to fill the dataset with a color
                datasetFill: true,
                // String - A legend template
                legendTemplate:
                    "<ul class='<%=name.toLowerCase()%>-legend'><% for (var i=0; i<datasets.length; i++){%><li><span style='background-color:<%=datasets[i].lineColor%>'></span><%=datasets[i].label%></li><%}%></ul>",
                // Boolean - whether to maintain the starting aspect ratio or not when responsive, if set to false, will take up entire container
                maintainAspectRatio: true,
                // Boolean - whether to make the chart responsive to window resizing
                responsive: true,
            };

            var salesChart = new Chart(salesChartCanvas, {
                type: "line",
                data: salesChartData,
                options: salesChartOptions
            });
        }
    }
};
</script>

<style lang="">
</style>




