<template>
    <div class="col-md-12">
        <div class="box box-widget widget-user">
            <div class="widget-user-header bg-aqua-active" style="height: 80px;">
                <h3 class="widget-user-username">{{ userInfo['username'] }}</h3>
                <h5 class="widget-user-desc">普通用户 &amp; {{ userInfo['create_time'] }}</h5>
            </div>
            <div class="widget-user-image" style="margin-top: -40px;">
                <img
                    class="img-circle"
                    :src="userInfo['header']?userInfo['header']:'static/img/user2-160x160.jpg'"
                />
            </div>
            <div class="box-footer">
                <div class="row">
                    <div class="col-md-6">
                        <div class="box-body">
                            <div class="form-group">
                                <label>用户名：</label>

                                <div class="input-group">
                                    <div class="input-group-addon">
                                        <i class="fa fa-calendar"></i>
                                    </div>
                                    <input
                                        type="text"
                                        class="form-control"
                                        data-inputmask="'alias': 'dd/mm/yyyy'"
                                        data-mask
                                    />
                                </div>
                            </div>
                            <div class="form-group">
                                <label>密码：</label>

                                <div class="input-group">
                                    <div class="input-group-addon">
                                        <i class="fa fa-calendar"></i>
                                    </div>
                                    <input
                                        type="password"
                                        class="form-control"
                                        data-inputmask="'alias': 'dd/mm/yyyy'"
                                        data-mask
                                    />
                                </div>
                            </div>

                            <div class="form-group"></div>

                            <!-- phone mask -->
                            <div class="form-group">
                                <label>联系方式：</label>

                                <div class="input-group">
                                    <div class="input-group-addon">
                                        <i class="fa fa-phone"></i>
                                    </div>
                                    <input
                                        type="text"
                                        class="form-control"
                                        data-inputmask="&quot;mask&quot;: &quot;(999) 999-9999&quot;"
                                        data-mask
                                    />
                                </div>
                            </div>

                            <div class="form-group">
                                <label>邮箱：</label>

                                <div class="input-group">
                                    <div class="input-group-addon">
                                        <i class="fa fa-envelope"></i>
                                    </div>
                                    <input
                                        type="text"
                                        class="form-control"
                                        data-inputmask="'mask': ['999-999-9999 [x99999]', '+099 99 99 9999[9]-9999']"
                                        data-mask
                                    />
                                </div>
                            </div>
                        </div>
                    </div>

                    <div class="col-md-6">
                        <div class="box-body">
                            <div class="form-group">
                                <label>出生日期：</label>

                                <div class="input-group date">
                                    <div class="input-group-addon">
                                        <i class="fa fa-calendar"></i>
                                    </div>
                                    <input
                                        type="text"
                                        class="form-control pull-right"
                                        id="datepicker"
                                    />
                                </div>
                            </div>

                            <div class="form-group">
                                <label>居住地：</label>

                                <div class="input-group">
                                    <div class="input-group-addon">
                                        <i class="fa fa-edit"></i>
                                    </div>
                                    <input
                                        type="text"
                                        class="form-control pull-right"
                                        id="reservation"
                                    />
                                </div>
                            </div>
                            <div class="form-group">
                                <label>AuthKey</label>

                                <div class="input-group">
                                    <div class="input-group-addon">
                                        <i class="fa fa-laptop"></i>
                                    </div>
                                    <input
                                        type="text"
                                        class="form-control"
                                        data-inputmask="'alias': 'ip'"
                                        data-mask
                                    />
                                </div>
                            </div>

                            <div class="form-group">
                                <label>Personality</label>

                                <div class="input-group">
                                    <div class="input-group-addon">
                                        <i class="fa fa-thumbs-o-up"></i>
                                    </div>
                                    <input
                                        type="text"
                                        class="form-control pull-right"
                                        id="reservationtime"
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <button
                    class="btn btn-success pull-right"
                    @click="openWarnMask({
                    'title': '不可执行',
                    'info': '测试阶段不开通修改个人信息功能'
                })"
                >确定修改</button>
            </div>
        </div>
        <warn-tip v-model="showWarnMask" :para="warnInfo"></warn-tip>
    </div>
</template>

<script>
import WarningTip from "../formComponents/WarningTip";
export default {
    data() {
        return {
            userInfo: {},
            showWarnMask: false,
            warnInfo: {}
        };
    },
    components: {
        "warn-tip": WarningTip
    },
    created() {
        this.getUserInfo();
    },
    methods: {
        openWarnMask(pa) {
            this.showWarnMask = true;
            this.warnInfo = pa;
        },
        getUserInfo() {
            this.$http.get("userinfo.user_info").then(res => {
                if (res.body.status) {
                    this.userInfo = res.body.user_info[0];
                } else {
                    this.$notify({
                        title: res.body.message,
                        type: "warning"
                    });
                }
            });
        }
    }
};
</script>






