<template>
    <div class="col-md-4">
        <p class="text-center">
            <strong>Active / Total</strong>
        </p>
        
        <div class="progress-group">
            <span class="progress-text">66Ip</span>
            <span class="progress-number">
                <b>{{ scale['66ip'] }}</b>
            </span>

            <div class="progress sm">
                <div class="progress-bar progress-bar-red" :style="{ width: scale['66ip_scale'] }"></div>
            </div>
        </div>

        <div class="progress-group">
            <span class="progress-text">快代理</span>
            <span class="progress-number">
                <b>{{ scale['kuaidaili'] }}</b>
            </span>

            <div class="progress sm">
                <div class="progress-bar progress-bar-green" :style="{ width: scale['kuaidaili_scale'] }"></div>
            </div>
        </div>

        <div class="progress-group">
            <span class="progress-text">Ip3366</span>
            <span class="progress-number">
                <b>{{ scale['ip3366'] }}</b>
            </span>

            <div class="progress sm">
                <div class="progress-bar progress-bar-yellow" :style="{ width: scale['ip3366_scale'] }"></div>
            </div>
        </div>

        <div class="progress-group">
            <span class="progress-text">西刺代理</span>
            <span class="progress-number">
                <b>{{ scale['xici'] }}</b>
            </span>

            <div class="progress sm">
                <div class="progress-bar progress-bar-aqua" :style="{ width: scale['xici_scale'] }"></div>
            </div>
        </div>

    </div>
</template>

<script>
export default {
    data() {
        return {
            scale: {},
        };
    },
    mounted() {
        this.getScale();
    },
    methods: {
        getScale() {
            this.$http.get("dashboard.total_active_scale").then(res => {
                this.scale = res.body.scale;
            });
        }
    }
};
</script>

<style lang="">
</style>



